.background {
  position: relative;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  overflow: hidden; }
  .background::before {
    content: "";
    position: absolute;
    top: -164px;
    right: -28px;
    z-index: 0;
    width: 732px;
    height: 732px;
    border-radius: 50%;
    background: radial-gradient(86.95% 86.95% at 102.6% 27.94%, #dd0505 0%, rgba(221, 5, 5, 0.12) 100%);
    opacity: 0.6;
    filter: blur(200px); }
    @media only screen and (min-width: 1920px) {
      .background::before {
        top: -8.54167vw;
        right: -1.45833vw;
        width: 38.125vw;
        height: 38.125vw; } }
  .background::after {
    content: "";
    position: absolute;
    bottom: -244px;
    left: -532px;
    z-index: 0;
    width: 876px;
    height: 876px;
    background: radial-gradient(86.95% 86.95% at 102.6% 27.94%, #dd0505 0%, rgba(221, 5, 5, 0.12) 100%);
    opacity: 0.2;
